<template>
	<view class="box" id="box">
		<view :class="{'article-hidden':!article.articleContent}" v-if="is_login">
			<view class="top-title font-bold">
				{{article.articleTitle}}
			</view>
			<view class="author">
				<image lazy-load class="img" :src="article.headPortrait" mode="" lazy-load></image>
				<view class="name">
					{{article.customerName}}
				</view>
			</view>
			<view class="author author-time">
				<view class="img time-img" src="" mode=""></view>
				<view class="name">
					{{article.issuedDate}}
				</view>
			</view>
			<jyf-parser class="good-detail" :html="article.articleContent" lazy-load ref="article" selectable
			 show-with-animation use-anchor>加载中...</jyf-parser>
			<!-- 底部浏览量 -->
			<view class="copy-tips">
				本文由网友编辑推荐，如有侵权请联系我们删掉
			</view>
			<view class="edit-btn bg-btn" @tap="edit">
				我也要制作
			</view>
			<view class="clear"></view>
			<view class="operate-area">
				<view class="operate-li">
					<view class="iconfont iconliulanliang"></view>
					<view class="num">
						{{article.readNum}}
					</view>
				</view>
				<view class="operate-li" @tap="getPraise">
					<view class="iconfont icondianzan" :class="{'active':isPraise}"></view>
					<view class="num">
						{{article.praiseNum}}
					</view>
				</view>
				<view class="no-btn operate-li">
					<button open-type="share" class="no-btn operate-li primary-border">
						<view class="iconfont iconfenxiang2"></view>
						<view class="num">
							分享
						</view>
					</button>
				</view>
				<navigator :url="'/pages/make_money/complaint?articleSeqId'+article.seqId+'&articleTitle='+article.articleTitle"
				 class="operate-li primary-border">
					<view class="iconfont icontousujianyi3x" style="color: #4872E5;"></view>
					<view class="num">
						投诉
					</view>
				</navigator>
			</view>
			<view class="adver-box">
				<image lazy-load v-if="cur_advert==6" class="plateform-ad" src="../../static/images/activity_banner.png" mode="aspectFill"></image>
				<!-- 图文广告 -->
				<view class="advertise_display" v-if="cur_advert==1">
					<image lazy-load class="img-box" src="/static/images/adver-pic-avatar.png" mode="aspectFill"></image>
					<view class="right">
						<view class="title text font-bold">
							广告标题
						</view>
						<view class="desc text">
							广告简介广告简介广告简介这还少积分地方和吉尔欧菲光穷鬼接费肥肉服务热返回分为非温服巍峨符合未发货放鸡岛见覅额文化服务IP而恢复䦹
						</view>
						<view class="tips">
							推广
						</view>
					</view>
				</view>
				<!-- 图片广告 -->
				<view class="advertise_display" v-if="cur_advert==2">
					<image lazy-load class="adver-img" src="/static/images/adver-pic-bg.png" mode=""></image>
				</view>
				<!-- 关注广告 -->
				<view class="advertise_focus" v-if="cur_advert==3">
					<image lazy-load class="img-box" src="/static/images/adver-pic-avatar.png" mode="aspectFill"></image>
					<view class="right">
						<view class="l">
							<view class="title text font-bold">
								广告标题
							</view>
							<view class="desc text">
								广告简介
							</view>
						</view>
						<view class="tips">
							+关注
						</view>
					</view>
				</view>
				<!-- 电话广告 -->
				<view class="advertise_phone" v-if="cur_advert==4">
					<image lazy-load class="img-box" src="/static/images/adver-pic-avatar.png" mode="aspectFill"></image>
					<view class="right">
						<view class="l">
							<view class="title text font-bold">
								广告标题
							</view>
							<view class="desc text">
								有任何疑问请来电咨询
							</view>
						</view>
						<view class="tips">
							<view class="iconfont icondianhua"></view>
							拨打
						</view>
					</view>
				</view>
			</view>
			<view class="blank"></view>
			<view class="make-btn" @tap="showShare"> 
				<view class="text">分享</view>
				<view class="text">朋友圈</view>
			 </view>
		</view>
		<view class="login-box" v-if="!is_login">
			<image lazy-load class="no-login" src="/static/images/message-box-icon.png" mode="aspectFill"></image>
			<button class="no-btn confirm-btn" open-type="getUserInfo" @getuserinfo="wxLogin">请先登录</button>
		</view>
		<view class="canvas-hidden">
			<wm-poster v-if="articlePic&&qrcodeSrc&&nickName" @success="posterSuccess" :imgSrc="articlePic" :QrSrc="qrcodeSrc" :Title="article.articleTitle" :Referrer="nickName"></wm-poster>
		</view>
		<uni-modal class="uni-modal-box" v-if="pop_show&&canvasSrc">
			<div class="uni-mask" @tap="pop_show=false"></div>
			<view class="qrcode-img">
				<image lazy-load @tap="saveImg" class="img" :src="canvasSrc" mode="widthFix" />
				<view class="tips">
					说明：长按保存图片分享到朋友圈
				</view>
			</view>
		</uni-modal>
		<!-- 我的分享二维码 -->
		<canvas class="qrcode-canvas canvas-hidden" canvas-id="qrcode" />
	</view>
</template>

<script>
	import wmPoster from "@/components/wm-poster/wm-poster.vue"
	import jyfParser from '@/components/jyf-parser/jyf-parser';
	import jwt_decode from "jwt-decode";
	import uQRCode from '@/common/js/uqrcode.js'
	import {
		getProvider,
		getCode
	} from '@/common/js/login/newlogin.js'
	import {
		mapState
	} from 'vuex';
	let seqId = ""; //文章id
	let offerRewardSeqId = ""; //悬赏id
	let sharebtn = "";
	let articleCode = ""; //分享人code
	let customerSeqId = "";
	let provider = "";
	let code = "";
	export default {
		data() {
			return {
				is_login: true,
				article: {},
				cur_advert: 10,
				isPraise: false,
				itemtitle: '',
				itemprice: '',
				itempic: '',
				articlePic: "",
				pop_show: false,
				qrcodeSrc: "",
				canvasSrc:"",
				share_params: "",
				nickName:""
			}
		},
		computed: {
			...mapState({
				user_state: state => state.user,
			})
		},
		onLoad(option) {
			// 是否登录
			let local_token = this.$common.getLocalSync('token');
			this.is_login = local_token ? true : false;
			
			if (option.q !== undefined) {
				// 通过朋友圈分享的二维码进入
				let q = decodeURIComponent(option.q);
				//截取参数 
				let share_url = getQueryString(q);
				seqId = getQueryString(q, "seqId"); //文章id
				offerRewardSeqId = getQueryString(q, "offerRewardSeqId"); //悬赏id
				articleCode = getQueryString(q, "articleCode"); //分享人code
				if (this.is_login) {
					this.getData()
				}
			} else {
				// 自己进入或者好友分享进入
				seqId = option.seqId; //文章id
				offerRewardSeqId = option.offerRewardSeqId; //悬赏id
				articleCode = option.articleCode? option.articleCode : ""; //分享人code
				sharebtn = option.sharebtn ? option.sharebtn : ""; //文章列表点击分享跳过来
				if (this.is_login) {
					this.getData()
				}
			}
		},
		onShow() {
			// #ifdef MP
			(async () => {
				//获取服务商信息
				provider = await getProvider();
				//获取code
				code = await getCode(provider[0])
			})()
			// #endif
		},
		components: {
			jyfParser,
			wmPoster
		},
		// #ifdef MP
		onShareAppMessage(res) {
			this.giveReward('SHARE');
			return {
				title: this.article.articleTitle,
				path: '/pages/make_money/article_detail?seqId=' + seqId + '&offerRewardSeqId=' + offerRewardSeqId+'&articleCode='+this.user_state.shareCode,
				imageUrl: this.article.articlePic
			}
		},
		// #endif
		methods: {
			getData(){
				customerSeqId =  this.$common.getLocalSync('customerSeqId') ;
				// 手机登录用username
				this.nickName=this.user_state.nickname?this.user_state.nickname:this.user_state.userName
				this.giveReward('READ')
				this.loadDetail();
				this.getIsPraise();
				this.getQrcode();
			},
			// 点击分享朋友圈
			showShare(){
				this.pop_show=true;
				if(!this.canvasSrc){
					this.$box.toast('海报生成中','none')
				}
			},
			// canvas图片成功
			posterSuccess(e) {
				console.log(e);
				this.canvasSrc = e.tempFilePath;
			},
			// 制作小程序二维码
			getQrcode() {
				let url=this.$webconfig.web_url+"/articleShare?seqId="+seqId+"&offerRewardSeqId="+offerRewardSeqId+'&articleCode='+this.user_state.shareCode;
				console.log(url);
				uQRCode.make({
					canvasId: 'qrcode',
					text: url,
					size: 100,
					margin: 5,
					success: res => {
						this.qrcodeSrc=res;
					},
					complete: () => {}
				})
			},
			
			// 是否点赞
			getIsPraise() {
				this.$api.get('/o2oArticlePraise/isPraise', {
					params: {
						offerRewardSeqId: offerRewardSeqId,
						customerSeqId: customerSeqId

					}
				}).then(res => {
					if (res.code == 200) {
						this.isPraise = res.data;
					}
				})
			},
			// 点赞
			getPraise() {
				this.$api.post('/o2oArticlePraise/praise', {
					offerRewardSeqId: offerRewardSeqId,
					customerSeqId: customerSeqId
				}).then(res => {
					if (res.code == 200) {
						if (res.data) {
							this.loadDetail();
							this.isPraise = !this.isPraise
						}
					}
				})
			},
			// 我要制作
			edit() {
				uni.navigateTo({
					url: '/pages/make_money/article_edit?seqId=' + seqId + '&offerRewardSeqId=' + offerRewardSeqId
				})
			},
			// 分享 阅读
			giveReward(type) {
				this.$api.post('/o2oOfferReward/shareOrReadGiveRewards', {
					rewardType: type,
					seqId: offerRewardSeqId,
					customerSeqId: customerSeqId,
					readSuperSeqId:articleCode
				}).then(res => {})
			},

			// 文章详情
			loadDetail() {
				let self = this;
				this.$api.get('/o2oMyArticle/getArticleInfo', {
					params: {
						seqId: seqId,
						offerRewardSeqId: offerRewardSeqId
					}
				}).then(res => {
					if (res.code == 200) {
						let data = res.data;
						this.article = data;
						data.praiseNum = data.praiseNum == null ? 0 : data.praiseNum;
						this.articlePic = this.article.articlePic;

						if (sharebtn) {
							this.$nextTick(function() {
								self.pageScrollToBottom()
							})
						}
					}
				})
			},
			// 滑动到最底部 方便分享
			pageScrollToBottom: function() {
				uni.createSelectorQuery().select('#box').boundingClientRect(function(rect) {
					// 使页面滚动到底部
					uni.pageScrollTo({
						scrollTop: rect.bottom,
						duration: 300
					});

				}).exec()
			},
			// 微信登录
			wxLogin(e) {
				// #ifndef H5
				let that = this;
				if (!code) {
					uni.showToast({
						icon: 'none',
						title: '正在加载中，稍等一下',
						duration: 1000
					});
				}
				if (e.detail && e.detail.errMsg == 'getUserInfo:ok') {
					uni.getUserInfo({
						success(info_res) {
							that.info_res = info_res;
							that.sendLogin()
						}
					})
				}
				// #endif
			},
			sendLogin() {
				this.$api.post('/login/save', {
					code: code,
					rawData: this.info_res.rawData,
					iv: this.info_res.iv,
					signature: this.info_res.signature,
					encryptedData: this.info_res.encryptedData
				}).then(res => {
					if (res.code == 200) {
						let token = res.data;
						let user_json = jwt_decode(token);
						this.$common.setLocal('token', res.data);
						this.$common.setLocal('customerSeqId', user_json.userId);
						this.$common.setLocal('customerName', user_json.userName || "");
						this.$store.commit('saveUser', user_json)
						this.is_login = true;
						// 登录之后再请求之前接口
						this.getData()
					}
				})
			},
			// 保存图片
			saveImg() {
				let self = this;
				uni.showActionSheet({
					title: '标题',
					itemList: ['保存图片到本地'],
					success: (e) => {
						uni.saveImageToPhotosAlbum({
							filePath: self.canvasSrc,
							success: function() {
								self.$box.confirm('已保存到本地相册，请打开相册查看', false);
								self.pop_show = false
							},
							fail: function(err) {
								self.saveImgFail(err)
							}
						});
					}
				})
			},
			// 保存图片失败
			saveImgFail(err) {
				if (err.errMsg === "saveImageToPhotosAlbum:fail auth deny" || err.errMsg ===
					"saveImageToPhotosAlbum:fail authorize no response" || err.errMsg ===
					"saveImageToPhotosAlbum:fail auth denied") { // 没有授权，重新授权，兼容iso和Android
					uni.showModal({
						title: '授权提示',
						content: "是否允许获取保存相册权限",
						success: (res) => {
							if (res.confirm) { // 点击确定，则调用相册授权
								uni.openSetting({
									success(settingdata) {
										if (settingdata.authSetting["scope.writePhotosAlbum"]) {
											uni.showToast({
												title: '获取权限成功，再次点击图片保存到相册'
											});
										} else {
											console.log("获取权限失败")
											uni.showToast({
												title: '请确定已打开保存权限',
												icon: "none"
											});
										}
									}
								})
							}
						}
					})
				} else if (err.errMsg === "saveImageToPhotosAlbum:fail file not found" || err.errMsg ===
					"saveImageToPhotosAlbum:fail file not exists" || err.errMsg ===
					"saveImageToPhotosAlbum:fail get file data fail"
				) { // 无图片，则提示
					uni.showToast({
						title: '暂无图片',
						icon: "none"
					});
				}
			}
		}
	}

	function getQueryString(url, name) {
		var reg = new RegExp('(^|&|/?)' + name + '=([^&|/?]*)(&|/?|$)', 'i')
		var r = url.substr(1).match(reg)
		if (r != null) {
			return r[2]
		}
		return null;
	}
</script>

<style lang="scss">
	.canvas-hidden {
		position: fixed;
		z-index: -10;
		left: -9999px;
	}
	.uni-modal-box {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 999;
		display: block;
		box-sizing: border-box;
	}

	.uni-mask {
		position: fixed;
		z-index: 999;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		background: rgba(0, 0, 0, .6);
	}

	.uni-modal-box .uni-modal {
		position: fixed;
		z-index: 999;
		width: 80%;
		max-width: 600rpx;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		background-color: #fff;
		text-align: center;
		border-radius: 6rpx;
		overflow: hidden;
	}

	.uni-modal-box .uni-modal__hd {
		padding: 1em 1.6em .3em;
	}

	.uni-modal-box .uni-modal__bd {
		padding: 1.3em 1.6em 1.3em;
		min-height: 80rpx;
		font-size: 30rpx;
		line-height: 1.4;
		color: #999;
		max-height: 800rpx;
		overflow-y: auto;
	}

	.uni-modal-box .uni-modal__ft {
		position: relative;
		line-height: 96rpx;
		font-size: 36rpx;
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
	}

	.uni-modal-box .uni-modal__btn {
		display: block;
		flex: 1;
		text-decoration: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		position: relative;
	}

	.uni-modal__btn_primary {
		color: $color-primary;
	}

	.qrcode-img {
		position: fixed;
		z-index: 1000;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		.img {
			width: 600rpx;
		}

		.tips {
			width: 100%;
			text-align: center;
			color: white;
			font-size: 22rpx;
			margin-top: 10rpx;
		}
	}


	.edit-btn {
		width: 200rpx;
		margin: 30rpx 0;
		float: right;
		clear: both;
	}

	.clear {
		width: 100%;
		height: 1rpx;
		clear: both;
	}

	.login-box {
		width: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		margin: 100rpx auto;

		.no-login {
			width: 200rpx;
			height: 160rpx;
		}
	}

	.confirm-btn {
		width: 100%;
		background: #ffa098;
		margin: 20px auto;
		height: 30px;
		line-height: 30px;
		border-radius: 50px;
		font-size: 28rpx;
		padding: 0;
		font-weight: bold;
		color: white;
		text-align: center;
		cursor: pointer;
	}

	.article-hidden {
		visibility: hidden !important;
	}

	.advertise_display {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		background: #fff;
		padding: 20rpx 30rpx;
		border-radius: 10rpx;

		.img-box {
			width: 160rpx;
			height: 160rpx;
			flex-shrink: 0;
			margin-right: 40rpx;
		}

		.right {
			flex-grow: 1;
			height: 124rpx;
			display: flex;
			flex-wrap: wrap;
			align-content: flex-start;
			position: relative;

			.text {
				width: 100%;
				font-size: 26rpx;
			}

			.title {
				height: 50rpx;
				line-height: 50rpx;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
				margin-top: -10rpx;
			}

			.desc {
				height: 64rpx;
				color: #666666;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.tips {
				position: absolute;
				right: 0;
				bottom: -36rpx;
				width: 80rpx;
				height: 36rpx;
				border: 2px solid rgba(245, 90, 77, 1);
				border-radius: 4rpx;
				color: #F55A4D;
				font-size: 26rpx;
				text-align: center;
				line-height: 36rpx;
			}
		}

		.adver-img {
			width: 100%;
			height: 200rpx;
		}
	}

	// 关注广告
	.advertise_focus {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		background: #fff;
		padding: 20rpx 30rpx;
		border-radius: 10rpx;

		.img-box {
			width: 160rpx;
			height: 160rpx;
			flex-shrink: 0;
			margin-right: 40rpx;
			border-radius: 100%;
		}

		.right {
			flex-grow: 1;
			height: 124rpx;
			height: 160rpx;
			display: flex;
			align-items: center;
			justify-content: space-between;

			.l {
				height: 120rpx;
				display: flex;
				align-content: space-between;
				flex-wrap: wrap;
			}

			.text {
				width: 100%;
				font-size: 26rpx;
			}

			.title {
				height: 50rpx;
				line-height: 50rpx;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
				margin-top: -10rpx;
			}

			.desc {
				color: #666666;
				overflow: hidden;
			}

			.tips {
				width: 98rpx;
				height: 46rpx;
				line-height: 46rpx;
				border: 2rpx solid rgba(245, 90, 77, 1);
				border-radius: 22rpx;
				color: #F55A4D;
				text-align: center;
				font-size: 26rpx;
			}
		}
	}

	// 电话广告
	.advertise_phone {
		display: flex;
		justify-content: space-between;
